import { Component, OnInit } from '@angular/core'; import { Router, ActivatedRoute } from '@angular/router'; import { Subscription } from 'rxjs/Subscription';  import { Classificacao_ouvidoria } from '../../../modelo/Classificacao_ouvidoria'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual'; import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'classificacao_ouvidoria-dados-edit',   templateUrl: 'classificacao_ouvidoria_dadosEdit.component.html' }) export class Classificacao_ouvidoriaDadosEditComponent implements OnInit {    classificacao_ouvidoria: Classificacao_ouvidoria;    private sub: Subscription;    private errorMessage: string;     constructor(private router: Router,               private route: ActivatedRoute,               private pesquisaService: PesquisaService) { }    ngOnInit() {       this.sub = this.route.parent.params.subscribe(params => {         let o : ObjetoVirtual = new ObjetoVirtual();         o.classeJava = "br.com.primum.modelo.classificacao_ouvidoria.Classificacao_ouvidoria";                 let id = +params['id'];         this.classificacao_ouvidoria = new Classificacao_ouvidoria();         this.classificacao_ouvidoria.cd_classificacao_ouvidoria = id;         o.objetoJson = this.classificacao_ouvidoria;          this.pesquisaService.efetuarPesquisa(o)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);       });   }    onEfetuarPesquisa(result : any) {     if (!result) {       this.errorMessage = 'Houve um erro ao pesquisa a classificacao_ouvidoria. ';       }      this.classificacao_ouvidoria = result;   }    ngOnDestroy() {     this.sub.unsubscribe();   }     onSubmit() {         }     onCancel(event: Event) {     event.preventDefault();     this.router.navigate(['/']);   }  }


